<script setup>
import { reactive } from 'vue';
import menu from './menu.json';

const props = defineProps({
  menuType: {
    type: String,
    default: 'options',
  },
});

const menuData = reactive(menu);

function getMenuHash(menu) {
  if (menu) {
    return props.menuType === 'options' ? menu.oHash : menu.cHash;
  }
  return '#';
}
</script>

<template>
  <ul class="aside-menu">
    <li v-for="item in menuData" :key="item.id">
      <a :href="getMenuHash(item.children[0])">{{ item.title }}</a>
      <ul>
        <li v-for="menuItem in item.children">
          <a :href="getMenuHash(menuItem)">{{ menuItem.title }}</a>
        </li>
      </ul>
    </li>
  </ul>
</template>

<style lang="less">
.aside-menu {
  font-size: 14px;
}
</style>
