<script setup>
import { reactive, ref } from 'vue';

const msg = ref('Hello Vue3');
const rawHtml = ref('<span style="color: red">This should be red.</span>');
const dynamicClass = ref('dynamic-class');
const isButtonDisabled = ref(true);
const objectOfAttrs = reactive({
  id: 'container_1',
  class: 'wrapper',
});
const number = ref(1);
const ok = ref(true);
const message = ref('Hello World');
const id = ref('x2');
const date = ref(2222222222222);
const seen = ref(true);
const attributeName = ref('href');
const url = ref('https://cn.bing.com');
const eventName = 'focus';

function formatDate(value) {
  const date = new Date(value);
  return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
}

function handleFocus() {
  console.log('input focus');
}
</script>

<template>
  <div class="template-syntax-c">
    <div>
      <h3>文本插值</h3>
      <span>Message: {{ msg }}</span>
    </div>

    <div>
      <h3>原始HTML</h3>
      <p>Using text interpolation: {{ rawHtml }}</p>
      <p>
        Using v-html directive:
        <span v-html="rawHtml"></span>
      </p>
    </div>

    <div>
      <h3>Attribute 绑定</h3>
      <div v-bind:class="dynamicClass">dynamicClass</div>
      <h4>简写</h4>
      <div :class="dynamicClass">dynamicClass simple</div>
      <h4>布尔型 Attribute</h4>
      <button :disabled="isButtonDisabled">Button</button>
      <h4>动态绑定多个值</h4>
      <div v-bind="objectOfAttrs">objectOfAttrs</div>
    </div>
  </div>

  <div>
    <h3>使用 JavaScript 表达式</h3>
    <div>{{ number + 1 }}</div>
    <div>{{ ok ? 'YES' : 'NO' }}</div>
    <div>{{ message.split('').reverse().join('') }}</div>
    <div :id="`list-${id}`">{{ `list-${id}` }}</div>
    <h4>调用函数</h4>
    <span>{{ formatDate(date) }}</span>
  </div>

  <div>
    <h3>指令 Directives</h3>
    <p v-if="seen">Now you see me</p>
    <h4>动态参数</h4>
    <a v-bind:[attributeName]="url" target="_blank">bing 搜索</a>
    <br />
    <input type="text" v-on:[eventName]="handleFocus" />
  </div>
</template>

<style></style>
